.cardContainer {
  display: block;
  width: 25%;
  padding: 10px;
  border: 1px solid rgb(235, 231, 231);
  border-radius: 3px;
  height: 360px;
  box-sizing: border-box;
  transition: 0.5s;
  // margin-right: 5px;

  .wrapper {
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: initial;
    width: 100%;
    height: 100%;
    position: relative;

    .wrapButton {
      position: absolute;
      bottom: 0;
      background-color: #fff;
      width: 100%;
      height: 70px;
      display: none;
      justify-content: space-between;
      align-items: center;
      button {
        width: 45%;
        border-radius: 20px;
        font-size: 13px;
        height: 50%;
        transition: 0.5s;
      }
      .inline {
        background-color: rgba(255, 116, 51, 1);
        color: #fff;
      }
      .outline {
        border: 1px solid rgba(255, 116, 51, 1);
      }

      button:hover {
        letter-spacing: 0.5px;
        font-weight: bold;
      }
    }

    .content {
      position: absolute;
      width: 100%;
      background-color: #fff;
      color: black;
      bottom: 0;
      right: 0;
      opacity: 0.7;
      left: 0;
    }
  }
}

.cardContainer:hover {
  border: 1px solid #fff;
  box-shadow: 1px 9px 36px -4px rgba(0, 0, 0, 0.75);
  .wrapper {
    .wrapButton {
      display: flex;
    }
    .content {
      transform: translateY(-70px);
      transition: transform 0.3s;

      opacity: 1;
    }
  }
}

@media screen and (max-with: 460px) {
  .cardContainer {
    width: 50%;
  }
}
@media screen and (max-with: 900px) {
  .cardContainer {
    width: 30%;
  }
}
@media screen and (min-with: 900px) {
  .cardContainer {
    width: 25%;
  }
}
