.shoppeHeader {
  background: rgb(246, 63, 46);
  background: radial-gradient(
    circle,
    rgba(246, 63, 46, 1) 0%,
    rgba(255, 116, 51, 1) 77%
  );
  height: 119px;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  .navBarWrap {
    height: 34px;
    background-color: transparent;
    display: flex;
    justify-content: space-between;
  }
  .wrapLogoSearch {
    height: 85px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .logo {
      font-size: 45px;
      color: #fff;
    }
    .searchAndKey {
      width: 100%;
      .keySearch {
        .navLink {
          border: none;
        }
      }
    }
    .textLogo {
      font-size: 35px;
      color: #ffff;
      font-family: "Open Sans", sans-serif;
    }
    ion-icon {
      font-size: 45px;
      color: #fff;
      margin: 0 20px;
    }
  }

  .navLink {
    color: #fff;
    font-size: 13px;
    text-decoration: none;
    border-right: 1px solid #fff;
    padding: 0 5px;
    ion-icon {
      color: #fff;
      margin: 0 5px;
      font-size: 13px;
    }
  }
}
